header.header{width:100%;margin-top:10.5%}header.header .main{display:flex;justify-content:center;flex-wrap:wrap;flex-direction:column}header.header .main .hi{display:flex;width:100%;margin-bottom:4.6%;padding-left:42.9%}header.header .main .hi svg.hi-svg{width:17%;height:100%}header.header .main .name{width:100%;margin-bottom:1%}header.header .main .last-name-about{display:flex;width:100%;margin-bottom:4.6%}header.header .main .last-name-about .last-name{width:125%}header.header .main .last-name-about .about{width:25%}header.header .main .last-name-about .about .me{width:100%;display:flex;align-items:center;height:100%;justify-content:space-between;padding-left:15%}header.header .main .last-name-about .about .me svg.plus{width:18%}header.header .main .last-name-about .about .me svg.about-me{width:70%}header.header .main .description-box{width:100%}header.header .main .description-box div.creative-director{padding-left:42.9%;width:63%;margin-bottom:3%}header.header .main .description-box div.currently{padding-left:42.9%;width:64.8%;margin-bottom:10.5%}

